@import '../../scss/variables';

.container {
  width: 50%;
  padding: 100px 80px;
  background: #f8fbff;
}
.title {
  @include main-title;
  margin-bottom: 10px;
  &::before {
    content: '';
    position: absolute;
    width: 184px;
    height: 4px;
    top: 0;
    right: 0;
    left: 0;
    margin: auto;
    background-color: $accent;
  }
}
.wrapper {
  max-width: 450px;
  margin: auto;
  text-align: center;
  margin-bottom: 10px;
}
@media screen and (max-width: 1024px) {
  .container {
    width: 100%;
    padding: 40px 80px;
  }
  .title {
    font-size: 32px;
    line-height: 36px;
  }
  .wrapper {
    max-width: none;
  }
}
@media screen and (max-width: 780px) {
  .container {
    padding: 40px 20px;
  }
}
@media screen and (max-width: 450px) {
  .container {
    padding: 20px 15px 30px;
  }
  .title {
    font-size: 24px;
    line-height: 28px;
    padding-top: 16px;
  }
}
