@import '../../scss/variables';

.list {
  flex-wrap: wrap;
}
.item {
  width: 44%;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
}
.pic {
  margin-bottom: 15px;
}

.text {
  max-width: 180px;
  text-align: center;
  line-height: 19px;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.list :global {
  .slick-arrow {
    height: 20px;
    width: 20px;
    background-color: none;
    top: 12%;

    &::before {
      content: url(../../assets/img/faq_arrow.svg);
    }
  }
  .slick-prev {
    transform: rotate(90deg);
    left: 0;
  }
  .slick-next {
    transform: rotate(-90deg);
    right: 0;
  }
  .slick-dots li button::before {
    font-size: 8px;
    color: $main-grey;
    border-radius: 40px;
  }
}

