@import '../../scss/variables';
.container {
  width: 50%;
  padding: 100px 80px;
  background: #f5f5f5;
}
.title {
  @include main-title;
  margin-bottom: 50px;
  max-width: 820px;
  &::before {
    content: '';
    position: absolute;
    width: 184px;
    height: 4px;
    top: 0;
    right: 0;
    left: 0;
    margin: auto;
    background-color: $accent;
  }
}
@media screen and (max-width: 1064px) {
  .container {
    width: 100%;
    padding: 40px 80px;
  }
  .title {
    font-size: 32px;
    line-height: 36px;
  }
}
@media screen and (max-width: 780px) {
  .container {
    padding: 40px 20px;
  }
}
@media screen and (max-width: 450px) {
  .container {
    padding: 30px 15px 40px;
    background: transparent;
  }
  .title {
    padding-top: 16px;
    font-size: 24px;
    line-height: 28px;
    margin-bottom: 90px;
  }
}
