@import '../../scss/variables';
.wrapper {
  background: linear-gradient(111.04deg, #e2e2e2 0.39%, rgba(255, 255, 255, 0) 86.46%);
  padding: 75px 0 93px 0;
  position: relative;
  &::after {
    position: absolute;
    content: url(../../assets/img/intro-decor-2.png);
    bottom: 0;
    left: 0;
    z-index: 1;
  }
  &::before {
    position: absolute;
    content: url(../../assets/img/intro-decor.png);
    top: 0;
    right: 0;
    z-index: 1;
  }
}
.title {
  @include main-title;
  margin-bottom: 70px;
  text-transform: uppercase;
  z-index: 2;
  &::before {
    content: '';
    position: absolute;
    width: 184px;
    height: 4px;
    top: 0;
    right: 0;
    left: 0;
    margin: auto;
    background-color: $accent;
  }
}

.container {
  position: relative;
  width: 850px;
  margin: auto;
  height: auto;
  z-index: 2;
}

.pic {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
}

@media screen and (max-width: 980px) {
  .wrapper {
    display: flex;
    flex-direction: column;
    &::after {
      display: none;
    }
    &::before {
      display: none;
    }
  }
  .pic {
    position: relative;
    order: -1;
    margin-bottom: 40px;
  }
  .wrapper {
    padding: 60px 20px 80px 20px;
  }
  .container {
    width: 100%;
  }
  .title {
    font-size: 32px;
    margin-bottom: 50px;
  }
}
@media screen and (max-width: 450px) {
  .title {
    font-size: 24px;
    line-height: 28px;
    margin-bottom: 50px;
    padding: 0;
    &::before {
      display: none;
    }
  }
  .wrapper {
    padding: 55px 15px 100px 15px;
    background: transparent;
  }
}
