@import '../../scss/variables';

.list {
  display: flex;
  flex-direction: column;
}
.item {
  border-bottom: solid 1px #e7ebf0;
  max-width: 700px;
}
.item:last-child {
  border-bottom: none;
}
.wrapper {
  display: flex;
  align-items: center;
}
.title {
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: $main-text;
  padding: 18px 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  text-align: left;
  cursor: pointer;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.title:hover {
  color: $accent;
  transition: all 0.2s ease;
}

.title::after {
  content: url(../../assets/img/faq_arrow.svg);
  width: 30px;
  height: 30px;
  display: block;
  stroke: aqua;
  text-align: center;
}
.icon {
  margin-right: 24px;
}
.content {
  max-height: 0;
  transition: all 0.3s ease;
  opacity: 0;
  max-width: 560px;
  padding-left: 60px;
}
.item :global {
  .active-content {
    max-height: 300px;
    transition: all 0.3s ease;
    opacity: 1;
    margin-bottom: 16px;
  }
}
.item :global {
  .active-title {
    color: $accent;
  }
  .active-title::after {
    transform: rotate(180deg);
  }
}
@media screen and (max-width: 1024px) {
  .list {
    align-items: center;
  }
  .item {
    max-width: none;
    width: 100%;
  }
  .content {
    max-width: none;
  }
}
@media screen and (max-width: 450px) {
  .icon {
    display: none;
  }
  .title {
    font-size: 16px;
  }
  .content {
    padding: 0;
  }
  .item :global {
    margin-bottom: 6px;
  }
  .title {
    padding: 6px 0;
  }
}
