@import '../../scss/variables';

.inner {
  padding: 30px 0 37px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav {
  margin-left: 90px;
  margin-right: auto;
}
.list {
  display: flex;
}
.item {
  margin-right: 60px;
  font-size: 16px;
  line-height: 150%;
  color: $main-grey;
  padding: 6px 24px;
  transition: all 300ms ease;
  border-radius: 8px;
  cursor: pointer;
  &:hover {
    color: $white;
    background-color: $accent;
    box-shadow: 0px 8px 16px rgba(0, 195, 104, 0.4);
  }
  &:active {
    opacity: 0.6;
  }
}
.item:last-child {
  margin-right: 0;
}
.link {
  color: inherit;
}
.button {
  font-size: 16px;
  line-height: 150%;
  color: $white;
  padding: 4px 21px;
  width: 118px;
  height: 32px;
  background: $accent;
  box-shadow: 0px 8px 16px rgba(0, 195, 104, 0.4);
  border-radius: 8px;
  border: none;
  cursor: pointer;
  moz-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  background-size: 300% 100%;
}
.button:hover,
.button:focus {
  background-position: 100% 0;
  moz-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  background-image: linear-gradient(to right, #25aae1, #00c368, #30dd8a, #2bb673);
}
.button:active {
  opacity: 0.6;
}
@media screen and (max-width: 1024px) {
  .inner {
    padding: 20px 20px;
  }
  .nav {
    margin-left: 40px;
  }
  .item {
    margin-right: 20px;
  }
}
@media screen and (max-width: 830px) {
  .nav {
    display: none;
  }
}
@media screen and (max-width: 420px) {
  .button {
    display: none;
  }
  .inner {
    padding: 10px;
    justify-content: center;
  }
}
