@import '../../scss/variables';
.list {
  display: flex;
  flex-wrap: wrap;
}
.item {
  display: flex;
  width: 44%;
  flex-direction: column;
  align-items: center;
  margin-bottom: 60px;
  &:hover .pic {
    stroke: $accent;
    transition: all 0.3s ease;
  }
}
.pic {
  margin-bottom: 15px;
}

.text {
  max-width: 180px;
  text-align: center;
  line-height: 19px;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.item:nth-child(1),
.item:nth-child(5) {
  display: flex;
  align-items: end;
  justify-self: end;
  margin-right: 40px;
}
.item:nth-child(2),
.item:nth-child(6) {
  align-items: start;
  margin-left: 55px;
}
.item:nth-child(6) {
  margin-bottom: 0;
}
.item:nth-child(5) {
  margin-bottom: 0;
}
.item:nth-child(3) {
  align-items: start;
  width: 50%;
}
.item:nth-child(4) {
  align-items: end;
  width: 50%;
}
.item:nth-child(1) {
  margin-bottom: 120px;
}
