@import './variables';

@import './components/all';
html,
body {
  height: 100%;
  color: #f5f5f5;
}
body {
  background-color: $background;
  font-family: Roboto;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: $main-text;
}
#root {
  height: 100%;
}
.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.content {
  flex: 1 0 auto;
}

.container {
  max-width: 1780px;
  margin: auto;
  padding: 0 10px;
}
.container--full {
  max-width: 1920px;
  margin: auto;
}
.container--flex {
  display: flex;
}
@media screen and (max-width: 1024px) {
  .container {
    padding: 0;
  }
  .container--flex {
    flex-direction: column-reverse;
  }
}
