@import '../../scss/variables';

.wrapper {
  position: relative;
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.item {
  width: 48%;
  display: flex;
  gap: 10px;
  align-items: flex-start;
}
.image {
  width: 97px;
  height: 97px;
}

.item:first-child {
  width: 100%;
  margin-bottom: 140px;

  .icon:first-of-type {
    display: block !important;
    position: absolute;
    left: -60px;
    top: -20px;
    transform: rotate(180deg);
    fill: $accent;
  }
  .icon:last-of-type {
    display: block !important;
    position: absolute;
    right: -20px;
    bottom: -20px;
    fill: $accent;
  }

  .image {
    margin-right: 40px;
    width: 187px;
    height: 187px;
  }
}
.item:nth-child(even) {
  flex-direction: row-reverse;
}
.icon {
  display: none;
}

.name {
  font-weight: 600;
}
.text {
  margin-bottom: 10px;
}
.item:first-child {
  margin-bottom: 60px;
}
@media screen and (max-width: 550px) {
  .wrapper {
    position: static;
  }
  .list {
    flex-direction: column;
  }
  .item:first-child {
    display: flex;
    flex-direction: column !important;
    align-items: center;
    width: 100%;
    margin-bottom: 35px;
    background: #ffffff;
    box-shadow: 0px 8px 16px rgba(0, 195, 104, 0.24);
    border-radius: 32px;
    border: 1px solid rgba($color: #00c36866, $alpha: 0.2);
    padding: 16px;
    padding-top: 82px;
    position: relative;

    .image {
      position: absolute;
      top: -50px;
      margin-right: 0;
      width: 97px;
      height: 97px;
    }
    .text {
      text-align: center;
    }
    .name {
      text-align: center;
    }

    .icon:first-of-type {
      left: 16px;
      top: 4px;
      transform: rotate(180deg);
      fill: #e7ebf0;
    }
    .icon:last-of-type {
      position: absolute;
      right: 16px;
      top: 24px;
      bottom: auto;
      fill: #e7ebf0;
    }
  }
  .item {
    width: 100%;
    display: flex;
    gap: 10px;
    align-items: flex-start;
    margin-bottom: 26px;
  }
  .item:last-child {
    margin-bottom: 0;
  }
  .item:nth-child(even) {
    flex-direction: row;
  }
  .item:nth-child(odd) {
    flex-direction: row-reverse;
  }
  .text {
    margin-bottom: 18px;
  }
}
